import React, { useState, useEffect } from 'react';
import { withRouter } from 'react-router-dom';
import {
  Form,
  Tabs,
  Table,
  Button,
  Modal,
  Row,
  Col,
  message,
  Input,
  DatePicker,
  Select,
} from 'antd';

import { injectIntl } from 'react-intl';
import { getProjects, getActors, getProcessTypes, addProject } from '../../services';

const { TabPane } = Tabs;
const FormItem = Form.Item;
const { Option } = Select;

const ProjectsCmp = props => {
  const {
    form,
    form: { getFieldDecorator },
    intl: { messages },
  } = props;

  const [visible, setVisible] = useState(false);
  const [loading, setLoading] = useState(true);
  const [projects, setProjects] = useState([]);
  const [actors, setActors] = useState([]);
  const [process, setProcess] = useState([]);

  const config = {
    rules: [{ type: 'object', required: true, message: 'Please select time!' }],
  };

  useEffect(() => {
    fetch();
    fetchActors();
    fetchProcess();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  const fetch = async () => {
    try {
      setLoading(true);
      const { data } = await getProjects();
      setProjects(data.projects);
      setLoading(false);
    } catch (error) {
      setLoading(false);
      message.error(error);
    }
  };

  const fetchActors = async () => {
    try {
      setLoading(true);
      const { data } = await getActors();
      setActors(data.actors);
      setLoading(false);
    } catch (error) {
      setLoading(false);
      message.error(error);
    }
  };

  const fetchProcess = async () => {
    try {
      setLoading(true);
      const { data } = await getProcessTypes();
      setProcess(data);
      setLoading(false);
    } catch (error) {
      setLoading(false);
      message.error(error);
    }
  };

  const handleReset = () => {
    form.resetFields();
  };

  const handleSubmit = e => {
    e.preventDefault();

    form.validateFields(async (err, values) => {
      if (!err) {
        const form = {
          name: values.name,
          dueDate: values.dueDate,
          projectProcess: values.projectProcess,
          PMId: values.PMId,
        };
        setLoading(true);
        try {
          await addProject(form);
          message.success(messages.default.success);
          handleReset();
          setVisible(false);
          fetch();
        } catch (error) {
          setVisible(true);
          setLoading(false);
          message.error(error.message);
        }
      }
    });
  };

  const columns = [
    {
      title: 'Id',
      dataIndex: 'id',
      key: 'id',
    },
    {
      title: 'Name',
      dataIndex: 'name',
      key: 'name',
    },
    {
      title: 'ClientId',
      dataIndex: 'UserId',
      key: 'UserId',
    },
    {
      title: 'Status',
      dataIndex: 'status',
      key: 'status',
    },
    {
      title: 'dueDate',
      dataIndex: 'dueDate',
      key: 'dueDate',
    },
    {
      title: 'startDate',
      dataIndex: 'startDate',
      key: 'startDate',
    },
    {
      title: 'endDate',
      dataIndex: 'endDate',
      key: 'endDate',
    },
    {
      title: 'Process',
      dataIndex: 'ProcessType',
      key: 'ProcessType',
      render: value => {
        return <div>{value.variableName}</div>;
      },
    },
    {
      title: 'PMId',
      dataIndex: 'Actor',
      key: 'Actor',
      render: value => {
        return (
          <div>
            {value.name} (<small>{value.email}</small>)
          </div>
        );
      },
    },
  ];

  const operations = (
    <React.Fragment>
      <Button
        loading={loading}
        onClick={() => {
          setVisible(true);
        }}
        type="primary"
        icon="smile"
      >
        Add
      </Button>
    </React.Fragment>
  );

  const formItemLayout = {
    labelCol: {
      xs: { span: 24 },
      sm: { span: 8 },
    },
    wrapperCol: {
      xs: { span: 24 },
      sm: { span: 16 },
    },
  };

  // projectProcess, PMId

  return (
    <div>
      <div style={{ padding: 24, background: '#fff', minHeight: 360 }}>
        <Tabs defaultActiveKey="all" tabBarExtraContent={operations}>
          <TabPane tab="Projects" key="all">
            <Table
              size="small"
              loading={loading}
              columns={columns}
              dataSource={projects}
              rowKey={record => record.id}
            />
          </TabPane>
        </Tabs>

        <Modal
          centered
          width={600}
          title="Add Actor"
          visible={visible}
          onCancel={() => {
            setVisible(false);
          }}
          footer={[
            <Button
              key="back"
              onClick={() => {
                setVisible(false);
              }}
            >
              {messages.default.cancel}
            </Button>,
            <Button key="submit" type="primary" loading={loading} onClick={handleSubmit}>
              {messages.default.create}
            </Button>,
          ]}
        >
          <Form {...formItemLayout}>
            <Row justify="center" type="flex">
              <Col xs={24}>
                <FormItem label="Name" hasFeedback>
                  {getFieldDecorator('name', {
                    rules: [
                      {
                        required: true,
                        // message: messages.eds.rules.node_id.required,
                      },
                    ],
                  })(<Input />)}
                </FormItem>
                <FormItem label="Due Date">
                  {getFieldDecorator('dueDate', config)(<DatePicker />)}
                </FormItem>
                <FormItem label="Process" hasFeedback>
                  {getFieldDecorator('projectProcess', {
                    rules: [
                      {
                        required: true,
                        // message: messages.eds.rules.model_id.required,
                      },
                    ],
                  })(
                    <Select defaultValue="lucy">
                      {process.map(item => (
                        <Option value={item.variableName}>{item.name}</Option>
                      ))}
                    </Select>
                  )}
                </FormItem>

                <FormItem label="Actor" hasFeedback>
                  {getFieldDecorator('PMId', {
                    rules: [
                      {
                        required: true,
                        // message: messages.eds.rules.model_id.required,
                      },
                    ],
                  })(
                    <Select defaultValue="lucy">
                      {actors.map(item => (
                        <Option value={item.id}>{item.name}</Option>
                      ))}
                    </Select>
                  )}
                </FormItem>
              </Col>
            </Row>
          </Form>
        </Modal>
      </div>
    </div>
  );
};
const WrappedProjectsCmp = Form.create()(injectIntl(ProjectsCmp));

export default withRouter(WrappedProjectsCmp);
